import React, { Component, PropTypes } from 'react';
import {Container} from 'themes/grid'
import resume from 'assets/Elliot_Yun_Resume.pdf'
import {Embed} from './About.style'

class About extends Component {

	/* What does this do? */
	static propTypes = {};

	render() {
		return (
			<Container>
				<Embed src={resume} height="1165px"/>
				{/*<embed src={resume} width="830px" height="1165px" align="center"/>*/}
			</Container>
		);
	}
}

export default About;


